.datetimer {
  width: 350px;
  position: absolute;
  display: none;
  z-index: 999;
  user-select: none;
  background: #fff;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
}
.datetimer .header {
  position: relative;
  text-align: center;
  line-height: 50px;
  background: #006cff;
  color: #fff;
  font-size: 25px;
}
.datetimer .header span {
  width: 60%;
  display: inline-block;
}
.datetimer .header .previousMonth,
.datetimer .header .nextYear {
  margin-left: 10px;
}
.datetimer li {
  list-style: none;
}
.datetimer .body {
  height: 350px;
  position: relative;
}
.datetimer .body .month-grid,
.datetimer .body .year-grid,
.datetimer .body .day-grid {
  background: #fff;
  position: absolute;
  top: 0;
  width: 100%;
  bottom: 0;
  left: 0;
}
.datetimer .body ol, .datetimer .body ul {
  padding: 0;
  margin: 0;
}
.datetimer .body .month-grid ol {
  background: #eee;
}
.datetimer .body .month-grid li {
  display: inline-block;
  width: 50px;
  line-height: 50px;
  text-align: center;
  box-sizing: border-box;
}
.datetimer .body .month-grid ul li {
  cursor: pointer;
}
.datetimer .body .month-grid ul li.active {
  background: #006cff;
  color: #fff;
}
.datetimer .body .month-grid ul li.disable {
  color: #ccc;
}
.datetimer .body .list-group {
  width: 80px;
  display: inline-block;
}
.datetimer .body .list-group ul {
  height: 320px;
}
.datetimer .body .list-group ul li {
  line-height: 30px;
}
.datetimer .body .list-group ul li.active {
  background: #006cff;
  color: #fff;
}
.datetimer .body .day-grid {
  display: none;
}
.datetimer .body .day-grid .list-group {
  width: 60px;
}
.datetimer .body .day-grid .list-group:first-child {
  margin-left: 80px;
}
.datetimer .body .year-grid {
  display: none;
}
.datetimer .body .year-grid .list-group:first-child {
  margin-left: 95px;
}
.datetimer .body .year-grid .fa-close,
.datetimer .body .day-grid .fa-close {
  position: absolute;
  top: 5px;
  right: 5px;
}
.datetimer .footer {
  background: #eee;
  position: relative;
  height: 30px;
}
.datetimer .footer input {
  width: 30px;
  height: 30px;
  font-size: 16px;
  text-align: center;
  border: none;
  box-sizing: border-box;
  background: transparent;
}
.datetimer .footer button {
  position: absolute;
  right: 0;
  width: 100px;
  background: green;
  color: #fff;
  height: 30px;
  border: none;
}

.list-group .title,
.list-group ul {
  width: 100%;
  text-align: center;
}
.list-group ul {
  overflow-y: auto;
  border: 1px solid #eee;
}
.list-group ul::-webkit-scrollbar {
  height: 6px;
  width: 6px;
  margin-right: 5px;
  background: #f5f5f5;
  transition: all 0.3s ease-in-out;
  border-radius: 0px;
}
.list-group ul::-webkit-scrollbar-track {
  -webkit-border-radius: 0px;
  border-radius: 0px;
}
.list-group ul::-webkit-scrollbar-thumb {
  -webkit-border-radius: 0px;
  border-radius: 0px;
  background: rgba(0, 0, 0, 0.5);
}
.list-group ul::-webkit-scrollbar-thumb:hover {
  background: rgba(0, 0, 0, 0.6);
}
.list-group ul::-webkit-scrollbar-thumb:active {
  background: rgba(0, 0, 0, 0.8);
}
.list-group ul::-webkit-scrollbar-thumb:window-inactive {
  background: rgba(0, 0, 0, 0.4);
}

@media screen and (max-width: 400px) {
  .datetimer {
    position: fixed;
    width: 100%;
    left: 0;
    bottom: 0;
  }
  .datetimer .body {
    height: 350px;
    width: 350px;
    margin: 0 auto;
  }
  .datetimer .body .month-grid li {
    width: 45px;
    line-height: 45px;
  }
  .datetimer .body .list-group ul {
    height: 320px;
  }
}